@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
#app-layout {
  min-height: 100vh;
}
#app-header {
  background-color: #47c2ff;
  height: auto;
  position: relative;
  overflow: hidden;
}
@media only screen {
  #app-header {
    padding: 20px;
  }
}
@media only screen and (min-width: 992px) {
  #app-header {
    padding: 24px 32px;
  }
}
button.account-action-button {
  color: white;
  border: 1px solid white;
  opacity: 0.8;
}
button.account-action-button:hover {
  opacity: 1;
  background-color: white;
  color: #0096e0;
  border: 1px solid white;
}
#account-actions.not-loggedin {
  margin-left: auto;
  display: flex;
  flex-flow: row wrap;
  width: min-content;
  justify-content: flex-end;
  align-content: space-around;
}
#account-actions.not-loggedin > button {
  display: block;
  pointer-events: all;
  width: fit-content;
}
#account-actions.loggedin {
  margin-left: auto;
  display: flex;
  flex-flow: row wrap;
  width: min-content;
  justify-content: center;
  align-content: space-around;
}
#account-actions.loggedin > button,
#account-actions.loggedin .username {
  display: block;
  pointer-events: all;
  width: fit-content;
}
#account-actions.loggedin > .username {
  line-height: 1;
  color: #052f5c;
  text-decoration: underline #052f5c;
}
.header-overlay {
  background: none;
  width: 100%;
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  z-index: 25;
  pointer-events: none;
}
.header-overlay > * {
  z-index: 1;
}
.header-overlay .logo-and-title {
  display: flex;
  flex-flow: row nowrap;
  height: fit-content;
}
.header-overlay .logo-and-title .app-logo {
  fill: white;
  height: auto;
}
@media only screen {
  .header-overlay .logo-and-title .app-logo {
    max-width: 180px;
  }
}
@media only screen and (min-width: 768px) {
  .header-overlay .logo-and-title .app-logo {
    max-width: 200px;
  }
}
@media only screen and (min-width: 992px) {
  .header-overlay .logo-and-title .app-logo {
    max-width: 250px;
  }
}
.header-overlay .logo-and-title .app-title {
  color: white;
  line-height: 1;
  margin: 0;
  font-weight: normal;
  text-transform: uppercase;
}
@media only screen {
  .header-overlay .logo-and-title .app-title {
    display: none;
  }
}
@media only screen and (min-width: 768px) {
  .header-overlay .logo-and-title .app-title {
    display: inline;
  }
}
@media only screen {
  .header-overlay .logo-and-title .app-title {
    width: auto;
    font-size: 2rem;
    margin-left: 0;
  }
}
@media only screen and (min-width: 768px) {
  .header-overlay .logo-and-title .app-title {
    width: 200px;
    font-size: 2rem;
    margin-left: 15px;
  }
}
@media only screen and (min-width: 992px) {
  .header-overlay .logo-and-title .app-title {
    width: 250px;
    font-size: 2.5rem;
    margin-left: 20px;
  }
}
.header-background {
  display: flex;
  flex-wrap: nowrap;
  justify-content: stretch;
  pointer-events: all;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: -46.63076582px;
}
.header-background div.skewed-div {
  display: flex;
  align-items: center;
  min-width: 1px;
  width: 100px;
  transition: background-color 500ms ease, width 500ms ease, flex 500ms ease;
  transform-origin: bottom right;
  transform: skew(25deg);
  margin-left: -1%;
  box-shadow: -6px 0 16px -8px rgba(0, 0, 0, 0.08), -9px 0 28px 0 rgba(0, 0, 0, 0.05), -12px 0 48px 16px rgba(0, 0, 0, 0.03);
  position: relative;
  cursor: pointer;
}
.header-background > :nth-child(1) {
  background-color: #021427;
}
@media only screen {
  .header-background > :nth-child(1) {
    flex: 1 0 180px;
  }
}
@media only screen and (min-width: 576px) {
  .header-background > :nth-child(1) {
    flex: 0.75 0 180px;
  }
}
@media only screen and (min-width: 768px) {
  .header-background > :nth-child(1) {
    flex: 0.75 0 420px;
  }
}
@media only screen and (min-width: 992px) {
  .header-background > :nth-child(1) {
    flex: 0.5 0 540px;
  }
}
.header-background > :nth-child(1):hover {
  background-color: #021931;
}
@media only screen {
  .header-background > :nth-child(1):hover {
    flex-grow: 1.25;
  }
}
@media only screen and (min-width: 576px) {
  .header-background > :nth-child(1):hover {
    flex-grow: 1;
  }
}
@media only screen and (min-width: 768px) {
  .header-background > :nth-child(1):hover {
    flex-grow: 1;
  }
}
@media only screen and (min-width: 992px) {
  .header-background > :nth-child(1):hover {
    flex-grow: 0.75;
  }
}
.header-background > :nth-child(2) {
  background-color: #052f5c;
  flex: 1 1;
}
.header-background > :nth-child(2):hover {
  background-color: #063466;
  flex-grow: 3;
}
.header-background > :nth-child(2):hover .skewed-div-contents > .icon-image,
.header-background > :nth-child(2):hover .skewed-div-contents > .icon-text {
  opacity: 1;
  transform: translateY(0);
}
.header-background > :nth-child(3) {
  background-color: #005c8a;
  flex: 1 1;
}
.header-background > :nth-child(3):hover {
  background-color: #006394;
  flex-grow: 3;
}
.header-background > :nth-child(3):hover .skewed-div-contents > .icon-image,
.header-background > :nth-child(3):hover .skewed-div-contents > .icon-text {
  opacity: 1;
  transform: translateY(0);
}
.header-background > :nth-child(4) {
  background-color: #0096e0;
  flex: 1 1;
}
.header-background > :nth-child(4):hover {
  background-color: #009dea;
  flex-grow: 3;
}
.header-background > :nth-child(4):hover .skewed-div-contents > .icon-image,
.header-background > :nth-child(4):hover .skewed-div-contents > .icon-text {
  opacity: 1;
  transform: translateY(0);
}
.header-background > :nth-child(5) {
  background-color: #47c2ff;
}
@media only screen {
  .header-background > :nth-child(5) {
    flex: 1 0 60px;
  }
}
@media only screen and (min-width: 576px) {
  .header-background > :nth-child(5) {
    flex: 0.75 0 60px;
  }
}
@media only screen and (min-width: 768px) {
  .header-background > :nth-child(5) {
    flex: 0.75 0 70px;
  }
}
@media only screen and (min-width: 992px) {
  .header-background > :nth-child(5) {
    flex: 0.5 0 75px;
  }
}
.skewed-div-contents {
  color: #ffffff;
  transform-origin: inherit;
  transform: skewX(-25deg) translateX(-24px);
  background-color: transparent;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  text-wrap: none;
  position: relative;
  width: 100%;
  height: 100%;
}
.skewed-div-contents > .icon-image {
  fill: #ffffff;
  opacity: 0.5;
  height: auto;
  transform: translateY(15px);
}
@media only screen {
  .skewed-div-contents > .icon-image {
    width: 42px;
  }
}
@media only screen and (min-width: 576px) {
  .skewed-div-contents > .icon-image {
    width: 48px;
  }
}
@media only screen and (min-width: 768px) {
  .skewed-div-contents > .icon-image {
    width: 50px;
  }
}
@media only screen and (min-width: 992px) {
  .skewed-div-contents > .icon-image {
    width: 56px;
  }
}
.skewed-div-contents > .icon-text {
  white-space: nowrap;
  text-align: center;
  line-height: normal;
  margin-top: 5px;
  text-transform: uppercase;
  opacity: 0;
  transform: translateY(30px);
}
.skewed-div-contents .icon-image,
.skewed-div-contents .icon-text {
  transition: transform 0.5s ease, opacity 0.5s ease;
}
#app-footer {
  background: #e6e9ef;
  color: black;
  padding: 32px 32px 20px;
  font-style: italic;
  justify-content: center;
}
#app-footer .footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#content-layout {
  background: #e6e9ef;
  padding: 0 24px;
}
