@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.AimTest {
  background: #e6e9ef;
  width: auto;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-content: flex-start;
  min-width: fit-content;
}
.AimTest svg:not(:root) {
  overflow: visible;
}
h1.page-title {
  text-align: center;
}
div.results-divider {
  width: 100%;
  color: rgba(75, 75, 78, 0.75);
  border-bottom: 1px solid rgba(75, 75, 78, 0.75);
  font-size: 1.3em;
  text-align: left;
  margin: 20px 0;
}
div.test-and-status {
  width: 100%;
}
div.test-area-container {
  background: #005c8a;
  display: flex;
  width: 100%;
}
svg.test-area {
  width: 100%;
  height: 100%;
  max-width: 1000px;
  min-width: 450px;
  margin: 0 auto;
  overflow: visible;
}
.results-row {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
@media only screen {
  .results-row {
    flex-wrap: wrap;
  }
}
@media only screen and (min-width: 768px) {
  .results-row {
    flex-wrap: nowrap;
  }
}
.results-row div.ant-card-body {
  display: flex;
  flex-flow: row;
}
div.results-card {
  margin-top: 24px;
}
.results-target {
  min-width: 100px;
  width: 100%;
  padding: 24px;
}
@keyframes target-pop-in {
  from {
    transform: scale(0.5);
  }
  to {
    transform: scale(1);
  }
}
svg.Target g.target-group {
  animation: 200ms cubic-bezier(0.34, 1.56, 0.64, 1) 0s normal none running target-pop-in;
}
svg.Target .target-ring {
  pointer-events: none;
}
svg.Target .target-ring:nth-of-type(2n + 0) {
  fill: #ffffff;
}
svg.Target .target-ring:nth-of-type(2n + 1) {
  fill: #dc0e5a;
}
svg.Target .target-ring.target-body {
  stroke: #ffffff;
  stroke-width: 2px;
  box-shadow: 10px 5px 5px black;
  pointer-events: auto;
}
svg.Target .target-point {
  fill: #ef8f1a;
  stroke: #98580b;
  cursor: pointer;
}
svg.Target,
svg.StartButton {
  cursor: pointer;
}
svg.Target.results {
  cursor: auto;
}
svg.StartButton g.start-button-group {
  animation: 300ms cubic-bezier(0.34, 1.56, 0.64, 1) 0s normal none running target-pop-in;
}
svg.StartButton g.start-button-group circle.button-body {
  stroke: #ffffff;
  stroke-width: 2px;
  fill: #742d7b;
}
svg.StartButton g.start-button-group text.button-text {
  text-anchor: middle;
  alignment-baseline: central;
  font-size: 3em;
  fill: white;
  pointer-events: none;
  user-select: none;
}
.test-status {
  display: grid;
  grid: auto / 1fr 1fr 1fr;
  justify-items: center;
  font-size: 1.5em;
}
.test-status .status {
  width: 30%;
  margin: 0 auto;
  text-align: center;
}
div.ResultsLayout.accuracy div.stats-and-table-wrapper div.results-card.table > div.ant-card-body {
  width: 100%;
  overflow-y: unset;
  flex: 1 1;
}
