@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.ReactionTimeTest {
  background: #e6e9ef;
  margin: 0 auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.page-title {
  text-align: center;
  width: 100%;
}
.page-subtitle {
  font-style: italic;
  text-align: center;
  margin: 0 0 24px;
  width: 100%;
}
.reaction-area {
  color: white;
  min-height: 500px;
  text-align: center;
  cursor: pointer;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 32px;
}
.reaction-area.unplayed {
  background: #86878a;
}
.reaction-area.unplayed .reaction-area-message {
  animation: fade-in 1s ease 1s both;
}
.reaction-area.failed {
  background: #ef8f1a;
}
.reaction-area.roundComplete {
  background: #005c8a;
}
.reaction-area.triggered {
  background: #05cc2a;
}
.reaction-area.waiting {
  background: #dc0e5a;
}
.reaction-area.results {
  background: #005c8a;
}
.reaction-area-message .message-main {
  font-size: 5em;
  text-transform: uppercase;
}
.reaction-area-message .message-subtitle {
  display: block;
  color: white;
  font-size: 2.5em;
}
.reaction-area-message .message-small-subtitle {
  font-size: 1.3em;
  font-style: italic;
  font-weight: lighter;
  padding-top: 10px;
}
.result-time {
  padding-top: 10px;
  min-width: fit-content;
}
.result-time div.ant-statistic-content {
  font-size: 6em;
  color: white;
}
.result-time span.ant-statistic-content-prefix {
  font-size: 0.5em;
}
.result-time span.ant-statistic-content-value {
  font-size: 1em;
}
.result-time span.ant-statistic-content-value-decimal {
  font-size: 0.7em;
}
.result-time span.ant-statistic-content-suffix {
  font-size: 0.7em;
}
.result-time.final-result span.ant-statistic-content-value {
  font-weight: bold;
}
.results-summary {
  display: flex;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 32px;
}
@media only screen {
  .results-summary {
    flex-flow: column nowrap;
  }
}
@media only screen and (min-width: 768px) {
  .results-summary {
    flex-flow: row-reverse;
  }
}
.results-summary .ScoreTable {
  flex-grow: 0;
  margin: 0;
  display: inline-block;
}
.results-summary .big-stats {
  min-width: max-content;
}
.results-summary .big-stats .stat-label {
  font-size: 1.2rem;
  font-weight: normal;
  margin-bottom: -24px;
  text-align: left;
  text-transform: uppercase;
}
.results-summary .big-stats .result-time {
  text-align: left;
}
.results-summary .divider {
  margin: 24px 0;
  border-right: none;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  width: 100%;
}
@media only screen and (min-width: 768px) {
  .results-summary .divider {
    border-right: 1px solid rgba(255, 255, 255, 0.5);
    margin: 0 24px;
    align-self: stretch;
    width: auto;
  }
}
div.results {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
  padding: 0;
}
div.lower-results {
  margin: 24px 0;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 24px;
}
div.lower-results div.results-stat-card {
  flex: 1 1 150px;
}
div.lower-results div.results-stat-card div.ant-statistic {
  display: inline-block;
  margin-bottom: 0;
  text-wrap: none;
}
div.lower-results div.results-stat-card > div.ant-card-body {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  gap: 20px;
}
div.lower-results div.results-stat-card > div.ant-card-body::before,
div.lower-results div.results-stat-card > div.ant-card-body::after {
  display: none;
}
div.lower-results div.histogram-card {
  flex-grow: 1;
  width: fit-content;
  height: fit-content;
}
div.lower-results div.histogram-card div.ant-card-body {
  display: flex;
  justify-content: center;
  height: auto;
}
div.lower-results div.ant-statistic {
  margin-bottom: 10px;
}
div.lower-results div.ant-statistic:last-child {
  margin-bottom: 0;
}
div.results-summary {
  display: flex;
  flex-direction: column;
}
