@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.ScoreTable {
  width: fit-content;
  margin: 0 auto;
  padding: 10px 0;
  text-align: center;
  border-collapse: collapse;
  font-size: 1.2rem;
  flex-grow: 0;
}
.ScoreTable table {
  table-layout: auto;
  width: 100%;
  text-align: right;
  vertical-align: baseline;
}
.ScoreTable table th {
  text-align: right;
  padding: 10px 15px;
  font-weight: normal;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.ScoreTable table td {
  font-size: 0.9em;
  padding: 5px 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.ScoreTable table td:last-of-type,
.ScoreTable table th:last-of-type {
  border-bottom: none;
}
.ScoreTable table td.delta {
  font-size: 0.8em;
  text-align: left;
  padding-right: 0;
  padding-left: 5px;
}
.ScoreTable table td.delta.negative {
  color: #06e52f;
}
.ScoreTable table td.delta.zero {
  color: #06e52f;
}
.ScoreTable table td.delta.positive {
  color: #f19b32;
}
.ScoreTable table tbody tr:last-of-type td {
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.ScoreTable table tbody tr:last-of-type td:last-of-type {
  border-bottom: none;
}
.ScoreTable table tfoot td {
  padding: 5px 15px;
  border-bottom: none;
}
div.ResultsLayout {
  padding: 0;
  display: flex;
  flex-flow: row wrap;
  align-items: stretch;
  justify-content: center;
  gap: 24px;
}
div.ResultsLayout div.results-card {
  display: flex;
  margin: 0;
  align-content: center;
}
@media only screen {
  div.ResultsLayout div.results-card > div.ant-card-body {
    padding: 10px;
  }
}
@media only screen and (min-width: 576px) {
  div.ResultsLayout div.results-card > div.ant-card-body {
    padding: 20px;
  }
}
@media only screen and (min-width: 992px) {
  div.ResultsLayout div.results-card > div.ant-card-body {
    padding: 24px;
  }
}
div.ResultsLayout div.results-card > div.ant-card-body::before,
div.ResultsLayout div.results-card > div.ant-card-body::after {
  display: none;
}
div.ResultsLayout div.stats-and-table-wrapper {
  flex: 3 1 250px;
  display: inline-flex;
  flex-flow: row wrap;
  gap: 20px;
}
div.ResultsLayout div.stats-and-table-wrapper div.results-card.table {
  flex-direction: column;
  flex: 2 1 250px;
}
div.ResultsLayout div.stats-and-table-wrapper div.results-card.table > div.ant-card-body {
  flex: 1 1 250px;
  width: 100%;
  overflow-y: auto;
  padding: 3px 0 0;
}
div.ResultsLayout div.stats-and-table-wrapper div.results-card.stats {
  flex: 2 1;
  flex-direction: column;
  align-content: center;
}
div.ResultsLayout div.stats-and-table-wrapper div.results-card.stats > div.ant-card-body {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  gap: 20px;
  justify-content: space-evenly;
  text-align: center;
}
div.ResultsLayout div.stats-and-table-wrapper div.results-card.stats > div.ant-card-body div.ant-statistic {
  display: inline-flex;
  width: max-content;
  text-wrap: none;
  flex-flow: column nowrap;
}
div.ResultsLayout div.results-card.histogram {
  flex: 5 1 600px;
  flex-flow: column nowrap;
  text-wrap: none;
}
div.ResultsLayout div.results-card.histogram > div.ant-card-body {
  width: 100%;
  height: auto;
}
div.ResultsLayout div.results-card.histogram > div.ant-card-body .Histogram {
  margin: 0 auto;
}
