@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.histogram-title {
  text-align: center;
}
.query-point {
  fill: #ef8f1a;
}
.query-point.mean {
  fill: #f3ab53;
  stroke: #ef8f1a;
  stroke-width: 1.5px;
}
.density-point {
  fill: steelblue;
}
svg.root-svg:not(:root) {
  overflow: visible;
  position: relative;
}
g.root-transform {
  visibility: visible;
  pointer-events: visible;
}
div.title-and-switch {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  justify-content: space-between;
}
div.title-and-switch h4 {
  font-size: 1.1em;
  margin-bottom: 0;
}
span.x-axis-title,
span.y-axis-title {
  font-size: 12pt;
  display: block;
  align-self: center;
  justify-self: center;
}
span.x-axis-title span.axis-units,
span.y-axis-title span.axis-units {
  font-style: italic;
}
span.x-axis-title {
  text-align: center;
}
span.y-axis-title {
  text-align: center;
  transform: rotate(-90deg) translateY(25%);
}
div.grid {
  display: grid;
  grid: auto 1fr / 30px 1fr;
}
.x-axis-text {
  text-anchor: end;
}
.mouse-crosshair {
  fill: #052f5c;
  stroke: none;
}
.vertical-line {
  stroke: #052f5c;
}
.density-area {
  fill: rgba(0, 150, 224, 0.45);
  stroke: #052f5c;
  stroke-width: 1px;
  stroke-linejoin: round;
}
.histogram-bar {
  stroke: #63676e;
  stroke-width: 1px;
  fill: #c7cedb;
}
.tooltip-text {
  text-size: 1em;
}
.settings-icon {
  position: absolute;
  font-size: larger;
  right: 24px;
  top: 24px;
  z-index: 3;
}
.settings-content {
  display: flex;
  flex-direction: column;
  min-width: 250px;
}
.bandwidth-slider {
  width: 100%;
}
.callout-background {
  fill: rgba(255, 255, 255, 0.8);
  stroke: #86878a;
}
.bandwidth-slider-label {
  margin-top: 15px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}
.bandwidth-slider-label span:nth-child(2) {
  font-style: italic;
}
.hist-content {
  display: grid;
  grid: auto auto auto / auto 1fr;
  height: fit-content;
}
.hist-content .hist-title {
  grid-area: 1/1/1/span 2;
  text-align: center;
  margin-bottom: 0;
  grid-column: span;
}
.hist-content div.hist {
  overflow: visible;
}
.hist-content span.axis-title {
  display: inline-block;
  font-size: 1.1em;
}
.hist-content span.axis-title.y {
  display: inline-block;
  text-align: center;
  writing-mode: vertical-rl;
  line-height: 2;
  transform: rotate(180deg) translateX(3px);
}
.hist-content span.axis-title.x {
  margin: 0 auto;
}
.hist-content span.axis-title .axis-title-unit {
  font-style: italic;
}
